import React, {useState} from 'react'

 
export default function Textform(props) {
const handleUpclick= ()=>{
    console.log("UPPER CASE IS CLICKED"+ text);
    let newText= text.toUpperCase();
    setText(newText)
props.showAlert("converted to uppercase", "success");
}
    const handleLoclick= ()=>{
    console.log("Lower CASE IS CLICKED"+ text);
    let newText= text.toLowerCase();
    setText(newText)
    props.showAlert("converted to lowercase", "success");
    
}
  const handleCopy = () => {
    navigator.clipboard.writeText(text)
      .then(() => {
        alert("Text copied to clipboard!");
      })
      .catch(err => {
        alert("Failed to copy: " + err);
         
      });
  };


    

const handleOnchange=(event)=>{
    console.log("on change");
    setText (event.target.value)
    
}

    const [text, setText] = useState('enter text here');
    //text=("new text");//this is wrong way
    //setText=("next text");//this is correct way
  return (
    <>
    <div className='container'style={{color:props.mode ==='dark'? 'white':'black'}}>
    <h1>{props.heading}</h1>

<div className="mb-3">
  
  <textarea className="form-control" value={text} onChange={handleOnchange} style={{backgroundColor: props.mode === 'dark' ? 'gray ' : 'white' ,color: props.mode=== 'dark'? 'white':'gray'}} id="ybox" rows="8"></textarea>
</div>
  <button type="button " class="btn btn-primary">Enable Dark Mode</button>

<button className="btn  primary mx-1" onClick={handleUpclick}>convert  into uppercase</button>
<button className="btn  primary mx-1" onClick={handleLoclick}>convert  into uppercase</button>

<button className="btn  btn- primary mx-1" onClick={handleCopy}>Copy</button>

    </div>
    <div className="container my-3"style={{color: props.mode=== 'dark'? 'white':'black'}}>
<h1>your text summery </h1>
<p>   {text.length} characters  </p>
<h2>reading time  </h2>
<p>{text.split(" ").filter(word => word.trim() !== "").length}: words, {text.length} Characters</p>
<h2>Preview</h2>
<p>{text}</p>
    </div>
    </>
  
  )
}

